import { useState, useEffect } from "react";
import {
  CardHeader,
  Card,
  CardContent,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from "@material-ui/core";
import Table from "components/TableDelete";
import CardMat from "components/Card/Card.js";
import CardHeaderMat from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";

export default function Clients({ clientsList, maj, setMaj }) {
  const [client, setClient] = useState({
    id: clientsList[0][0],
    libelle: clientsList[0][1],
  });

  const [tableHeaders] = useState([
    "Type aliments",
    "Date",
    "Quantite",
    "Commentaire",
  ]);
  const [tableData, setTableData] = useState("");

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  useEffect(() => {
    const url = "/api/clients/" + client.id;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTableData(response.achats);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [client, maj]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item>
        <Card>
          <CardHeader title="Gestion des clients" />
          <CardContent>
            <Grid container spacing={1} justify="center" alignItems="center">
              <Grid item xs={12}>
                <FormControl style={{ minWidth: "150px" }}>
                  <InputLabel id="Client-label">Client</InputLabel>
                  <Select
                    labelId="Client-label"
                    id="Client-select"
                    value={client.id}
                    onChange={(e) =>
                      setClient({
                        ...client,
                        id: e.target.value,
                        libelle: e.target.id,
                      })
                    }
                  >
                    {clientsList.map((c, key) => (
                      <MenuItem key={key} value={c[0]} id={c[1]}>
                        {c[1]}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={6}>
                {tableHeaders === "" || tableData === "" ? null : (
                  <CardMat style={{ marginTop: "50px" }}>
                    <CardHeaderMat color="info">
                      <Typography variant="h4">
                        Compra de {client.libelle}
                      </Typography>
                    </CardHeaderMat>
                    <CardBody>
                      <Table
                        tableHeaderColor="info"
                        tableHead={tableHeaders}
                        tableData={tableData}
                        postUrl={"/api/clients"}
                        maj={maj}
                        setMaj={setMaj}
                      />
                    </CardBody>
                  </CardMat>
                )}
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeaderMat style={{ marginTop: "1em" }} color="primary">
                    Stats dans le futur
                  </CardHeaderMat>
                  <CardBody>
                    <Grid
                      container
                      spacing={1}
                      justify="center"
                      alignItems="center"
                    >
                      <Grid item>
                        <h4>En moyenne commande toute les x semaines</h4>
                      </Grid>
                    </Grid>
                  </CardBody>
                </Card>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}
